:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.rootFixed {
	position: relative;
	display: inline-block;
}

.contentFixed {
	position: absolute;
	z-index: 100;
	z-index: var(--zindex-tooltip);
}

.bottomFixed .contentFixed {
	bottom: 0;
	left: 50%;
	-webkit-transform: translate(-50%, 100%);
	        transform: translate(-50%, 100%);
}

.leftFixed .contentFixed {
	left: 0;
	top: 50%;
	-webkit-transform: translate(-100%, -50%);
	        transform: translate(-100%, -50%);
}

.rightFixed .contentFixed {
	right: 0;
	top: 50%;
	-webkit-transform: translate(100%, -50%);
	        transform: translate(100%, -50%);
}

.topFixed .contentFixed {
	top: 0;
	left: 50%;
	-webkit-transform: translate(-50%, -100%);
	        transform: translate(-50%, -100%);
}



